.user-putPassword-container {
    max-width: 100%;
    width: 100%;
    margin: 0 auto;
}

// 
.user-putPassword-form {
    padding: 30px 50px 50px;
    width: 60%;
    background-color: #fff;
    height: auto;
    margin: 0 auto;
    // border: 2px solid #00b894;
    border-radius: 5px;
    // filter: drop-shadow(5px 5px 4px #5e5e5e);

    @include media768 {
        width: 80%;
    }

    @include media568 {
        width: 100%;
    }

    @include media414 {
        padding: 30px 10px;
    }

    // 
    .user-putPassword-title {
        font-size: 2.3rem;
        font-weight: bolder;
        padding: 10px 0;
        margin: 0 auto 15px;
        text-align: center;
        // color: #00b894;
        color: #000;
        width: 50%;
        // border-radius: 5px;
        position: relative;
        border-bottom: 2px solid #00b894;

        @include media320 {
            font-size: 2rem;
        }

        // //  icon 
        // .user-putPassword-edit-icon {
        //     width: 30px;
        //     height: 30px;
        //     margin: 0 10px 0 0;
        // }
    }

    // 
    label {
        display: block;
        padding: 15px 0 5px;
        // margin: 10px 0 0;
        font-size: 1.1rem;
        font-weight: bolder;
    }

    // 
    input[type='text'],
    input[type='password'] {
        padding: 10px 0 10px 10px;
        width: 100%;
        border-radius: 5px;
        border: 2px solid #636e72;
        margin: 0 0 5px;

        &:focus {
            outline: none !important;
            border: 2px solid #000000 !important;
            box-shadow: 0 0 10px #000000 !important;
        }
        &:active {
            outline: none !important;
            border: 2px solid #000000 !important;
            box-shadow: 0 0 10px #000000 !important;
        }
    }

    // 
    .inputValid {
        outline: none !important;
        border: 2px solid #00b894 !important;
        box-shadow: 0 0 5px #00b894;
    }

    //  
    .inputInvalid {
        outline: none !important;
        border: 2px solid #b00020 !important;
        box-shadow: 0 0 5px #b00020;
    }

    // 
    .putPassword-notice {
        display: block;
        padding: 30px 0 0;
        color: #b00020;
        font-size: 0.9rem;
        text-align: center;
    }

    // 
    .inputInvalidText {
        color: #b00020;
        font-weight: bolder;
    }

    // 
    input::placeholder {
        font-size: 0.9rem;
        // font-weight: bolder;
    }

    //  &  
    .putPassword-label-password,
    .putPassword-label-checkPassword,
    .putPassword-label-oldPassword {
        position: relative;

        //  &  icon
        .eye-hidden-password,
        .eye-open-password,
        .eye-hidden-checkPassword,
        .eye-open-checkPassword,
        .eye-hidden-oldPassword,
        .eye-open-oldPassword {
            position: absolute; // 
            top: 120%;
            right: 3%;
            width: 25px;
            height: 25px;
            fill: #636e72;
        }
    }

    // icon
    .eye-open-icon {
        display: none;
    }

    // class
    .eye-hidden {
        display: none;
    }
    .eye-open {
        display: block;
    }

    // 
    .user-putPassword-submit-btn {
        margin: 20px 0 0;
        width: 100%;
        color: #fff;
        height: 40px;
        padding: 5px 0;
        font-size: 1.2rem;
        font-weight: bolder;
        background-color: #00b894;
        border: 2px solid black;
        border-radius: 5px;
        cursor: pointer;
        transition: all 0.3s ease-in-out;

        // 
        &:hover {
            outline: none !important;
            border: 2px solid #00b894;
            box-shadow: 0 0 10px #25dab5;
        }
        &:active {
            outline: none !important;
            border: 2px solid #00b894;
            box-shadow: 0 0 10px #25dab5;
        }
    }
}
