// 
.home-photos-container {
    // padding: 100px 0 0;
    max-width: 100%;
    width: 100%;
    margin: 100px auto 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.home-photos-title {
    text-align: center;
    width: 100%;
    padding: 20px 0 30px;
    font-size: 2.5rem;
    font-weight: bolder;

    a {
        color: #00b894;
    }

    @include media768 {
        width: 100%;
        font-size: 2rem;
    }

    @include media320 {
        font-size: 1.8rem;
    }
}

// grid 
.home-photos-grid {
    padding: 10px;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(20px, 1fr)); // 
    grid-template-rows: 200px 200px 350px; // 
    gap: 10px; //  rows
    justify-content: center;
    background-color: black;

    // 
    grid-template-areas:
        'b1 b1 s1 s2'
        'b1 b1 s3 s4'
        's5 b2 b2 b2';

    // RWD 414 
    @include media414 {
        .home-photos-sm3,
        .home-photos-sm4,
        .home-photos-sm5 {
            display: none;
        }
        // 
        grid-template-rows: 200px 200px 200px;
        // 
        grid-template-areas:
            'b1 b1 b1 b1'
            's1 s1 s2 s2'
            'b2 b2 b2 b2';
    }

    //  skeleton 
    .home-photos-items-skeleton {
        width: 100%;
        height: 100%;
    }

    // 
    .home-photos-big1 {
        grid-area: b1;
    }
    .home-photos-big2 {
        grid-area: b2;
    }
    .home-photos-sm1 {
        grid-area: s1;
    }
    .home-photos-sm2 {
        grid-area: s2;
    }
    .home-photos-sm3 {
        grid-area: s3;
    }
    .home-photos-sm4 {
        grid-area: s4;
    }
    .home-photos-sm5 {
        grid-area: s5;
    }
}

// 
.home-photos-items {
    border: 2px solid rgba(255, 255, 255, 0.486);
    overflow: hidden;
    transition: all 0.5s ease-in-out;

    img {
        &:hover,
        &:active {
            transform: scale(1.1); // 
            filter: brightness(1.1); // 
        }
    }

    // &:hover img,
    // &:active img {
    //     transform: scale(1.1); // 
    //     filter: brightness(1.1); // 
    // }
}
// 
.home-photos-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    transition: all 0.5s ease-in-out;
    filter: brightness(0.8); // 
    cursor: pointer;
}
