.header-container {
    max-width: 100vw;
    width: 100%;
    font-size: 0.9rem;
    font-weight: bolder;
    position: fixed;
    z-index: 99; // 
    background-color: #fff;
}

// ---- top bar start ----

// 
.navbar-topTip-text {
    width: 100%;
    padding: 7px 0;
    background-color: #00b894;
    text-align: center;
}
// 
.header-top-container {
    width: 100%;
    margin: 0 auto;
    padding: 2px 0 1px;
}
// 
.header-top-inner-container {
    max-width: 100%;
    width: 70%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: relative;

    @include media414 {
        width: 100vw;
        justify-content: center;
        font-size: 0.9rem;
    }

    @include media375 {
        font-size: 0.8rem;
    }

    // li|
    li:nth-of-type(1)::after {
        content: '|';
        position: absolute;
        right: -10%;
        width: 3px;
        height: 20px;

        @include media320 {
            right: 2%;
            top: 30%;
        }
    }
    li:nth-of-type(2)::after {
        content: '|';
        position: absolute;
        right: -10%;
        width: 3px;
        height: 20px;

        @include media320 {
            right: 2%;
            top: 30%;
        }
    }
}

// 
.header-top-items {
    padding: 0 0 0 20px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    //  ( svg icon & font-color )
    &:hover .header-top-icon {
        fill: #00b894;
    }
    &:hover a {
        color: #00b894;
    }
    &:active .header-top-icon {
        fill: #0984e3;
    }
    &:active a {
        color: #0984e3;
    }

    @include media375 {
        width: 50%;
    }

    @include media320 {
        padding: 0 5px 0 0;
    }
}

// 
.header-cart-number {
    width: 25px;
    height: 25px;
    background-color: #00b894;
    color: black;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 0 0 3px;
}

// 
.header-top-cart {
    display: inline-block;
    // max-width: 300px;
    // width: 95%;
    width: 500px;
    // height: 300px;
    height: auto;
    max-height: 100vh;
    // background-color: #0984e3;
    background-color: #fff;
    color: black;
    position: absolute;
    z-index: 20;
    top: 115%;
    left: 50%;
    transform: translateX(-50%);
    transition: all 0.5 ease-in-out;
    opacity: 0;
    border: 2px solid black;
    overflow: hidden;
}

// 
.header-cart-items {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    // flex-direction: column;
    margin: 0 0 5px;
    border-bottom: 2px solid #00b894;
    padding: 5px 0 10px;
    flex-wrap: wrap;
    user-select: none;
}

.header-cart-image {
    display: inline-block;
    width: 100%;
    max-width: 30px;
    height: auto;
    max-height: 40px;
    object-fit: cover;
    object-position: center;
    border: 1px solid #dfe6e9;
    margin: 0 5px 0 0;
}

.header-cart-quantity {
    // background-color: black;
    // color: #fff;
    display: block;
    width: 50%;
    text-align: right;
    position: relative;

    &:after {
        content: 'x';
        position: absolute;
        right: -20%;
    }
}
.header-cart-price {
    // background-color: black;
    // color: #fff;
    display: block;
    width: 50%;
    text-align: center;
    font-style: italic;
    color: #636e72;
}
//  icon 
.header-cart-animate {
    @include animate(cartAnimate, 1s, ease-in, 0s);
    @include keyframes(cartAnimate) {
        0% {
            transform: translateY(0);
        }
        50% {
            transform: translateY(-20%);
        }
        100% {
            transform: translateY(0);
        }
    }
}

// 
.header-cart-show {
    @include animate(showCart, 3s, ease-in, 0s);
    @include keyframes(showCart) {
        0% {
            opacity: 0;
        }
        50% {
            opacity: 1;
        }
        100% {
            opacity: 0;
        }
    }
    // overflow: scroll; // 
}
// 
.header-cart-hidden {
    opacity: 0;
}

// 
.header-theme-text {
    padding: 0 0 0 7px;
}
// ---- top bar end ----

// 
.header-top-text {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    transition: color 0.3s ease-in-out;
}
