.user-info-put-container {
    padding: 20px;
    // max-width: 800px;
    width: 100%;

    // 
    .user-info-name-container {
        padding: 0 0 20px;
        // border-bottom: 2px solid #00b894;
        border-bottom: 2px solid black;

        // 
        .user-info-put-name {
            display: flex;
            align-items: center;
            font-size: 1rem;
            flex-wrap: wrap;

            //  icon 
            .user-info-name-icon {
                height: 20px;
                width: 20px;
                margin: 0 6px 0 0;
            }

            // 
            .user-info-put-number {
                display: block;
                margin: 0 0 0 25px;
                padding: 0 0 0 25px;
                position: relative;

                @include media414 {
                    width: 100%;
                    margin: 2px 0 0 0;
                }

                //  | 
                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 2px;
                    height: 100%;
                    background-color: #00b894;

                    @include media414 {
                        display: none;
                    }
                }
            }

            // 
            .user-info-put-origin {
                display: block;
                margin: 0 0 0 25px;
                padding: 0 0 0 25px;
                position: relative;

                @include media414 {
                    width: 100%;
                    margin: 5px 0 0 0;
                }

                //  | 
                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 2px;
                    height: 100%;
                    background-color: #00b894;

                    @include media414 {
                        display: none;
                    }
                }
            }
        }

        // 
        .user-info-logout-btn {
            margin: 15px 0 0;
            background-color: #636e72;
            padding: 7px;
            border: 2px solid #000;
            font-size: 1rem;
            color: #fff;
            border-radius: 5px;
            transition: all 0.3s ease-in-out;
            cursor: pointer;

            &:hover {
                background-color: #000;
            }
        }

        // 
        .user-info-delete-btn {
            margin: 15px 0 0 10px;
            background-color: #b00020;
            padding: 7px;
            border: 2px solid #000;
            font-size: 1rem;
            color: #fff;
            border-radius: 5px;
            transition: all 0.3s ease-in-out;
            cursor: pointer;

            &:hover {
                background-color: #000;
            }
        }
    }

    //  - 
    .user-info-put-form {
        padding: 20px 0 80px;
        clear: both; // 

        @include media414 {
            padding: 20px 0 0;
        }

        // 
        .user-info-title {
            font-weight: bolder;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 0 15px;

            //  icon 
            .user-info-edit-icon {
                height: 20px;
                width: 20px;
                margin: 0 2px 0 0;
            }
        }

        span {
            display: block;
        }

        label {
            display: inline-block;
            margin: 20px 0 0; // 
            font-size: 1rem;
            width: 140px;

            @include media414 {
                display: block; // 
                width: 100%;
            }
        }

        // 
        input[type='text'],
        input[type='password'],
        input[type='date'],
        input[type='number'],
        input[type='search'] {
            padding: 6px 6px 6px 10px;
            border: 1px solid #636e72;
            margin: 0 0 5px;
            width: 200px;
            border: 1px solid #636e72;

            @include media414 {
                margin: 5px 0 0;
                width: 100%;
            }

            &:focus {
                outline: none !important;
                border: 1px solid #000000 !important;
                box-shadow: 0 0 10px #000000 !important;
            }
            &:active {
                outline: none !important;
                border: 1px solid #000000 !important;
                box-shadow: 0 0 10px #000000 !important;
            }
        }
        // 
        select[type='select'] {
            padding: 5px;
            width: 150px;
            margin: 0 5px 0 0;
            border: 1px solid #636e72;

            @include media414 {
                margin: 5px 0 0;
            }

            &:focus {
                outline: none !important;
                border: 1px solid #000000 !important;
                box-shadow: 0 0 10px #000000 !important;
            }
            &:active {
                outline: none !important;
                border: 1px solid #000000 !important;
                box-shadow: 0 0 10px #000000 !important;
            }
        }

        //    ()
        .info-label-address-area {
            width: 0;

            @include media414 {
                margin: 0; // 
            }
        }

        //   ()
        .info-label-address-site {
            display: inline-block;
            width: 0px;
        }

        //  
        .info-input-address-site {
            width: 300px !important; // 
            // width: 40% !important; // 
            @include media768 {
                margin: 5px 0 5px 140px !important;
                width: 400px !important;
            }
            @include media568 {
                width: 305px !important;
            }

            @include media414 {
                width: 100% !important;
                margin: 5px 0 0 0 !important; // 
            }
        }

        // // 
        // .inputValid {
        //     outline: none !important;
        //     border: 1px solid #00b894 !important;
        //     box-shadow: 0 0 5px #00b894;
        // }

        //  
        .inputInvalid {
            outline: none !important;
            border: 1px solid #b00020 !important;
            box-shadow: 0 0 5px #b00020;
        }

        // 
        .inputInvalidText {
            color: #b00020;
            font-weight: bolder;
            margin: 0 0 0 140px;

            @include media768 {
                margin: 5px 0 0 140px;
            }

            @include media568 {
                margin: 5px 0 0 0;
            }

            @include media414 {
                margin: 5px 0 0 0;
            }
        }

        //  &  &  &  &  
        .info-name-notice,
        .info-email-notice,
        .info-birthday-notice,
        .info-tel-notice,
        .info-address-notice {
            display: block;
            margin: 3px 0 10px 140px;
            font-size: 0.9rem;
            max-width: 800px;
            width: 100%;
            color: #636e72;
            white-space: wrap;

            @include media568 {
                margin: 5px 0 5px 0;
            }

            @include media414 {
                width: 100%; // 
                line-height: 1.1rem;
            }
        }

        // 
        .address-cancel-btn {
            margin: 0 0 0 10px;
            padding: 0 0 0 8px;
            background-color: #fff;
            border: none;
            position: relative;
            font-size: 0.9rem;
            color: #636e72;
            cursor: pointer;

            &:hover,
            &:active {
                color: #000;
            }

            //  icon
            .user-info-cancel-icon {
                position: absolute;
                top: 50%;
                left: 0;
                transform: translate(-50%, -50%);
                width: 20px;
                height: 20px;
                margin: 0 2px 0 0;
            }
        }

        // 
        .info-cancel-btn {
            display: block;
            float: right;
            margin: 30px 0 0 10px;
            // margin: 20px auto 0;
            width: 100px;
            height: 40px;
            background-color: #fff;
            padding: 7px;
            border: 2px solid #636e72;
            color: #000;
            font-size: 1rem;
            font-weight: bolder;
            border-radius: 5px;
            cursor: pointer;

            @include media414 {
                width: 45%;
                float: none; // 
                display: inline-block; // 
            }

            &:hover,
            &:active {
                color: #b00020;
            }
        }

        // 
        .info-confirm-btn {
            display: block;
            float: right;
            margin: 30px 0 0 10px;
            width: 100px;
            height: 40px;
            background-color: #00b894;
            padding: 7px;
            border: 2px solid #636e72;
            font-size: 1rem;
            font-weight: bolder;
            border-radius: 5px;
            cursor: pointer;
            transition: all 0.3s ease;

            @include media414 {
                width: 45%;
                float: none; // 
                display: inline-block; // 
            }

            &:hover,
            &:active {
                color: #000;
                border: 2px solid #b00020;
                background-color: #fff;
                color: #b00020;
            }
        }
    }

    .swal2-default-outline {
        color: #000 !important;
    }
}
