// 
.home-hot-container {
    max-width: 100%;
    width: 100%;
    margin: 100px auto 0;
    // padding: 100px 0 0;
}

// 
.home-hot-swiper {
    width: 40%;

    // X
    @include media568 {
        width: 80%;
    }
    @include media414 {
        width: 70%;
    }
    @include media375 {
        width: 100%;
    }

    .swiper {
        width: 240px;
        // height: 350px;
        height: auto;

        @include media414 {
            width: 200px;
            padding: 20px 0 0;
        }

        @include media320 {
            padding: 20px 0 20px;
        }
    }

    // swiper
    .swiper-slide {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 18px;
        font-size: 22px;
        font-weight: bold;
        color: #fff;
    }
}
//------ swiper  start

// skeleton 
.home-hot-items-skeleton {
    width: 240px;
    height: 350px;
}

// 
.home-hot-items {
    position: relative;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
}

// 
.home-hot-img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
    border-radius: 5px;
    transition: all 0.5s ease-in-out;

    &:hover {
        transform: scale(1.2);
    }
    &:active {
        transform: scale(1.2);
    }
}

//  & 
.home-hot-btn {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    padding: 15px 0;
    background-color: rgba(0, 0, 0, 0.65);
    text-align: center;
    color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);

    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;

    @include media375 {
        font-size: 1rem;
    }
    @include media320 {
        font-size: 0.9rem;
    }
}

// 
.home-hot-small {
    font-size: 1rem;
    @include media375 {
        font-size: 0.8rem;
    }
}
// 
.home-hot-slogan {
    width: 50%;
    padding: 50px 100px 50px 0;
    margin: 0 0 0 5%;
    position: relative;

    @include media1140 {
        width: 40%;
    }

    @include media768 {
        width: 50%;
        // margin: 0 0 0 5%;
    }
    @include media568 {
        margin: 0 auto;
    }

    // 568 100%
    @include media568 {
        position: absolute;
        text-align: center;
        top: -8%;
        right: 0;
        height: 550px;
        max-width: 100%;
        width: 100%;
        padding: 0;

        @include media414 {
            height: 480px;
        }
        @include media375 {
            height: 480px;
        }
        @include media320 {
            height: 420px;
        }
    }
}
// 
.home-hot-title {
    font-size: 2.5rem;
    font-weight: bolder;

    @include media768 {
        width: 100%;
        font-size: 2rem;
    }

    @include media320 {
        font-size: 1.8rem;
    }
}
// 
.home-hot-subtitle {
    font-size: 2rem;
    font-weight: bolder;
    color: #00b894;
}

// 
.home-hot-small {
    margin: 5px 0 0;
    font-size: 0.9rem;
    // color: #636e72;
}

// 
.home-hot-list {
    padding: 0 0 0 5%;
    @include media768 {
        font-size: 1rem;
    }

    // 568 
    @include media568 {
        display: none;
    }

    li {
        // list-style: circle;
        // list-style-type: '\00BB';
        list-style-image: linear-gradient(0deg, #ffffff, #00b894);
        margin: 0 0 10px;
    }
}

// 
.home-hot-more {
    display: block;
    text-align: center;
    position: absolute;
    bottom: 0;
    right: 10%;
    border-radius: 5px;
    width: 120px;
    padding: 10px 0;
    background-color: #00b894;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    font-weight: bolder;
    color: black;
    font-size: 1.1rem;
    transition: color 0.5s ease-in-out;

    @include media414 {
        width: 100%;
        right: 0;
        bottom: -2%;
    }

    @include media320 {
        width: 100%;
        bottom: -15%;
    }

    // ()
    &::before {
        content: '';
        background-color: #b80012;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        transition: opacity 0.5s ease-in-out;
        width: 100%;
        height: 100%;
        opacity: 0;
        z-index: -1;
    }

    &:hover::before {
        opacity: 1;
    }

    &:hover {
        z-index: 2;
        color: white;
    }
    &:active {
        z-index: 2;
        color: white;
    }
}
//------ slogan  end

// 
.home-hot-content {
    max-width: 1140px;
    width: 100%;
    padding: 0 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    flex-wrap: wrap;
    position: relative;

    @include media768 {
        padding: 0;
    }

    @include media568 {
        padding: 54px 0 10px;
    }
    @include media414 {
        width: 100%;
        margin: 0 auto;
    }
}
