.product-info-container {
    width: 100%;
    // height: 500px;
    height: auto;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
}

// 
.product-info-images {
    width: 45%;
    // height: auto;
    height: 450px;
    // background-color: blueviolet;

    @include media414 {
        width: 100%;
    }
}

// 
.product-image-big {
    width: 100%;
    height: 80%;
    // height: 200x;
    // max-width: 440px;
    // max-height: 440px;
    // background-color: crimson;

    // 
    .product-big-image {
        display: block;
        width: 100%;
        height: 100%;
        // height: 200px;

        object-fit: cover;
        object-position: center;
        border: 2px solid #dfe6e9;
        cursor: zoom-in; // 
        transition: all 0.2s ease-in-out;

        &:hover {
            border: 2px solid #111414;
            box-shadow: 0 0 2px #111414;
        }
    }
}

// 
.product-image-small {
    width: 100%;
    height: 20%;
    // background-color: rgb(192, 146, 235);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

// 
.product-small-items {
    width: 75px;
    height: 75px;
}

// 
.product-small-image {
    display: block;
    width: 75px;
    // width: 100%;
    height: 75px;
    // height: auto;
    object-fit: cover;
    object-position: center;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    border: 2px solid #dfe6e9;

    &:hover {
        border: 2px solid #111414;
        box-shadow: 0 0 2px #111414;
    }
}

// 
.product-share-container {
    width: 100%;
    font-size: 1rem;
    color: #636e72;
    padding: 10px 0;
    display: flex;
    // justify-content: center;
    align-items: center;
    user-select: none;

    @include media414 {
        display: none;
    }

    // 
    .product-share-icon {
        margin: 0 0 0 5px;
    }

    .facebook {
        margin: 0 0 0 15px;
        width: 24px;
        height: 24px;

        &:hover,
        &:active {
            fill: #1877f2;
        }
    }

    .line {
        width: 27px;
        height: 27px;
        margin: 0 0 0 7px;

        &:hover,
        &:active {
            fill: #00c300;
        }
    }

    .twitter {
        width: 28px;
        height: 28px;

        &:hover,
        &:active {
            fill: #1da1f2;
        }
    }
}
// 
.product-image-show {
    width: 100vw;
    height: 100vh;
    position: fixed; // 
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #00000049;
    z-index: 99; // 

    @include media568 {
        justify-content: center;
        align-items: flex-start;
    }
}

//  + icon 
.product-image-show-content {
    position: relative;
}

//  icon 
.image-show-close {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 40px;
    height: 40px;
    cursor: pointer;
    transition: all 0.2s ease-in-out;

    &:hover,
    &:active {
        fill: #b80012;
    }
}

//  
.product-info-content {
    width: 55%;
    padding: 0 20px;
    position: relative;

    @include media414 {
        width: 100%;
    }
}

// 
.product-info-name {
    font-size: 2rem;
    font-weight: bolder;
    margin: 0 0 15px;
    position: relative;

    // 
    white-space: wrap;
    max-width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;

    @include media414 {
        margin: 15px 0;
        font-size: 1.5rem;
    }
}

// 
.product-info-price {
    width: 100%;
    padding: 0 0 15px;
    margin: 0 0 15px;
    font-size: 1.5rem;
    font-weight: bolder;

    @include media414 {
        font-size: 1.2rem;
    }

    .info-price-text {
        display: inline-block;
    }
}

// 
.product-info-description {
    font-size: 1.1rem;
    font-weight: bolder;
    position: relative;
    padding: 0 0 15px;
    margin: 0 0 15px;
    line-height: 1.5rem;

    // 
    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: #dfe6e9;
    }
}

// 
.product-info-color {
    // @extend .product-info-size;
    display: flex;
    align-items: center;
    padding: 0 0 15px;
    margin: 0 0 15px;
    position: relative;

    // 
    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: #dfe6e9;
    }
}

.product-color-label {
    margin: 0 5px 0 0;
    font-weight: bolder;
}

.product-color-input {
    height: 20px;
    width: 20px;
}

// 
.product-info-size {
    width: 100%;
    padding: 10px 0;
    position: relative;
    font-weight: bolder;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 0 5px;
}

// 
.info-size-text {
    display: inline-block;
    font-weight: bolder;
}

// 
.info-size-guide-text {
    display: inline-block;
    font-weight: bolder;
    background-color: #81ecec;
    padding: 5px 10px;
    @include flex-center;
    cursor: pointer;
    transition: all 0.3s ease-in;

    &:hover {
        filter: brightness(1.1);
        box-shadow: 0 0 5px #81ecec;
    }

    // icon
    .info-size-guide-icon {
        width: 20px;
        height: 20px;
    }
}
// 
.info-size-guide-content {
    width: 93%;
    height: auto;
    padding: 10px 12px;
    background-color: #fff;
    border: 2px solid #81ecec;
    box-shadow: 0 0 5px #81ecec;
    position: absolute;
    left: 50%;
    top: 8%;
    transform: translateX(-50%);
    z-index: -1;
    opacity: 0;
    transition: all 0.3s ease-in-out;

    h4 {
        text-align: center;
        font-weight: bolder;
        font-size: 1.3rem;
        margin: 5px 0 10px;
    }

    span {
        display: block;
        // display: flex;
        // justify-content: space-between;
        // text-align: left;
        color: #636e72;
        margin: 0 0 5px;
        font-weight: bolder;

        strong {
            display: inline-block;
            font-size: 1.1rem;
            font-weight: bolder;
            margin: 0 15px 0 0;
            color: #000;
        }
    }
}

// 
.size-list-container {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
    padding: 0 0 15px;

    // 
    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: #dfe6e9;
    }
}

// 
.size-list-items {
    display: inline-block;
    width: calc(100% / 3 - 10px);
    text-align: center;
    padding: 10px 10px;
    margin: 0 8px 8px 0;
    font-weight: bolder;
    cursor: pointer;
    background-color: #f5f5f5;
    transition: all 0.2s ease-in-out;

    @include media375 {
        width: calc(100% / 2 - 10px);
    }
}
// hover
.size-list-hover {
    &:hover {
        border: 2px solid #000 !important;
        box-shadow: 0 0 5px #000;
        text-decoration: underline;
    }
}
// 
.product-info-stock {
    width: 100%;
    position: relative;
    font-weight: bolder;
    padding: 15px 0;

    // 
    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: #dfe6e9;
    }
}

// 
.info-stock-text {
    display: inline-block;
    font-weight: 400;
}

// 
.product-info-number {
    width: 100%;
    padding: 15px 0 10px;
}

// 
.number-label {
    font-weight: bolder;
    margin: 0 0 10px;
    display: block;
}

// 
.product-number-content {
    display: flex;
}

// 
.number-input {
    display: inline-block;
    width: 80px;
    height: 40px;
    padding: 5px 5px 5px 18px;
    text-align: center;
    border: 2px solid #ccc;
    font-size: 1rem;

    &:focus,
    &:active {
        outline: none;
        border: 2px solid black;
        box-shadow: 0 0 10px black;
    }
}

// 
.number-add-btn,
.number-reduce-btn {
    outline: none;
    border: 2px solid #ccc;
    width: 40px;
    height: 40px;
    padding: 5px 10px;
    cursor: pointer;
    font-size: 1.5rem;
    // font-weight: bolder;
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover,
    &:active {
        color: #00b894;
    }
}

.number-add-btn {
    border-left: none;
}
.number-reduce-btn {
    border-right: none;
}

// 
.number-max-text {
    display: inline-block;
    padding: 5px 0 0;
    font-size: 1rem;
    color: #b80012;
    width: 100%;
}
// 
.product-info-bt-container {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    // background-color: blueviolet;
    position: relative;
    padding: 15px 0;
    margin: 0 0 5px;

    @include media768 {
        flex-direction: column;
    }

    // 
    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: #dfe6e9;
    }
}

// 
.product-info-btn {
    width: calc(100% / 3 - 6px);
    // margin: 0 3px;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 1.1rem;
    font-weight: bolder;
    border: 2px solid #dfe6e9;
    transition: all 0.2s ease-in-out;
    margin: 5px 0 0 0;

    @include media768 {
        width: 100%;
    }
}

// 
.product-info-addCart {
    background-color: #00b894;

    &:hover,
    &:active {
        background-color: #fff;
        color: #00b894;
        border: 2px solid #00b894;
    }
}

// 
.product-info-payment {
    background-color: #0a2240;
    color: #fff;

    &:hover,
    &:active {
        background-color: #fff;
        color: #0a2240;
        border: 2px solid #0a2240;
    }
}

// 
.product-info-addWish {
    background-color: #dfe6e9;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    &.isOpen {
        color: #b80012;
    }

    &:hover,
    &:active {
        color: #b80012;
    }

    .product-wish-after {
        color: #b80012;
    }
}

// 
.product-shop-title,
.product-delivery-title,
.product-store-title {
    font-size: 1.2rem;
    font-weight: bolder;
    margin: 0 10px 5px 0;
    display: inline-block;

    @include media375 {
        font-size: 1rem;
    }
}

.product-shop-text,
.product-delivery-text,
.product-store-text {
    font-size: 1.1rem;
    font-weight: 600;

    @include media375 {
        font-size: 0.9rem;
    }
}

.product-shop-title {
    color: #636e72;
}
.product-shop-text {
    color: #636e72;
}

// has (:) 
body:has(.info-size-guide-text:hover) .info-size-guide-content {
    opacity: 1;
    z-index: 30;
}
