// nav 
.product-sidebar-menu {
    width: 100%;
    padding: 20px 0 10px;
}

// 
.product-sidebar-title {
    width: 100%;
    cursor: pointer;
    padding: 8px 10px;
    outline: none;
    border: none;
    text-align: left;
    font-size: 1.8rem;
    font-weight: bolder;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 0 5px;
}

.product-title-icon {
    height: 25px;
    width: 25px;
}

//  li
.product-sidebar-items {
    position: relative;
    padding: 2px 0;

    // 
    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        background-color: #dfe6e9;
        // width: 85%;
        width: 95%;
        height: 2px;
    }
}

// 
.product-sidebar-btn {
    background-color: #fff;
    width: 100%;
    cursor: pointer;
    outline: none;
    border: none;
    text-align: left;
    font-size: 1rem;
    font-weight: bolder;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;

    &:hover,
    &:active {
        // 
        a {
            color: #00b894;
        }

        // 
        &.sidebar-btn-active a {
            color: #000;
        }
    }
}

// 
.sidebar-btn-active {
    background-color: #00b094;

    // 
    a {
        &:hover {
            color: #000000;
        }
    }
}

// icon 
.product-sidebar-icon {
    display: flex;
    // float: right;
    // background: blue;
    transition: all 0.5s ease-in-out;
    width: 25px;
    height: 25px;
    justify-content: center;
    align-items: center;
}

// 
.product-sidebar-text {
    overflow: hidden; // 
    height: 0; // 
    background-color: #fff;
}

// 
.product-sidebar-link {
    display: block;
    width: 100%;
    font-weight: bolder;
    padding-left: 15px; // 
    padding-top: 8px;
    padding-bottom: 8px;

    &:hover,
    &:active {
        background: #e6ebea6c;
    }
}
// 
.open {
    height: auto;
}
